@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  text-align: center;
  background-image:url(/bleusreactportfolio/static/media/background-img.96daa13c.jpg);
  background-size: auto;
  background-repeat: no-repeat;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
  font-weight: 400;
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #faf9f6;
}

h1 {
  margin-bottom: 10px;
}

h2 {
  font-size: 14pt;
  font-weight: 100;
  margin-bottom: 25px;
}

h3 {
  font-size: 12pt;
}

p {
  font-size: 12pt;
  letter-spacing: 2px;
  text-align: justify;
}

img {
  border-radius: 1000px;
}

a {
  color: #faf9f6;
  text-decoration: none;
}

ul {
  display: flex;
  margin: 0;
  text-align: center;
}

li {
  list-style-type:none;
  padding-left: 5px;
  padding-right: 20px;
}

footer {
  position:fixed;
  left:0;
  bottom:0;
  right:0;
  margin-bottom:10px;
}

.nav-item {
  text-align: center;
  padding-top: 25px;
}

.nav-link {
  margin: 35px;
}

span {
  position: relative;
  display: block;
  cursor: pointer;
}

span:before, span:after {
  content: '';
  position: absolute;
  width: 0%;
  height: 1px;
  top: 50%;
  margin-top: -0.5px;
  background: #efece4;
}

span:before {
  left: -2.5px;
}

span:after {
  right: 2.5px;
  background: #efece4;
  transition: width 0.8s cubic-bezier(0.22, 0.61, 0.36, 1);
}

span:hover:before {
  background: #8f4f69;
  width: 100%;
  transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

span:hover:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

.bio {
    margin: auto;
    width: 50%;
    padding: 20px 20px;
    margin-bottom: 75px;
    background-color: #8f4f69;
    border-radius: 20px;
    box-shadow: rgb(17 12 46 / 15%) 0px 48px 100px 0px;
}

.navbar-container {
  display: inline-block;
  list-style-type: none;
}

.portfolio .container {
  display: flex;
  flex-direction: column;
}

.grid {
  text-align: center;
